html {
  overflow-x: auto;
  overflow-y: scroll;
}
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font-weight: inherit;
  font-style: normal;
  vertical-align: baseline;
  background: transparent;
  border: 0;
  outline: 0;
}
body {
  box-sizing: border-box;
  background-color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  text-align: left;
}
a {
  text-decoration: none;
}
table {
  border-collapse: collapse;
}
img {
  border: 0 none;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
body {
  box-sizing: border-box;
}
.apiContentPanel {
  overflow-x: auto;
  width: 100%;
  background-color: #fff;
  overflow-y: scroll;
  height: 100%;
}
.apiContentPanel .downloadContent {
  padding-left: 40px;
}
.apiContentPanel h2 {
  height: 50px;
  padding-bottom: 10px;
  font-size: 24px;
  font-family: MicrosoftYaHei;
  color: #212121;
  line-height: 40px;
}
.apiContent {
  height: auto;
}
.baseinfoPanel {
  width: 82%;
}
.baseinfoPanel h1 {
  height: 96px;
  padding: 24px 0 36px 0;
  line-height: 38px;
  font-size: 30px;
  font-family: MicrosoftYaHeiSemibold;
  color: #333333;
}
.baseinfoPanel .baseinfo {
  height: 156px;
  padding-bottom: 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.baseinfoPanel .baseinfo div {
  height: 18px;
  font-size: 14px;
  font-family: MicrosoftYaHei;
  color: #666666;
  line-height: 18px;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.reqparampanel,
.reqbodyparam,
.structexample,
.apitool {
  width: 82%;
  padding-bottom: 36px;
}
.reqparampanel table,
.reqbodyparam table,
.structexample table,
.apitool table {
  width: 100%;
  border-radius: 4px;
}
.reqparampanel table tr,
.reqbodyparam table tr,
.structexample table tr,
.apitool table tr {
  border-left: 1px #f2f2f2 solid;
  border-right: 1px #f2f2f2 solid;
  border-top: 1px #f2f2f2 solid;
  padding-left: 20px;
  display: flex;
  align-items: center;
}
.reqparampanel thead,
.reqbodyparam thead,
.structexample thead,
.apitool thead {
  width: 100%;
  background: #f2f2f2;
}
.reqparampanel thead tr,
.reqbodyparam thead tr,
.structexample thead tr,
.apitool thead tr {
  height: 36px;
}
.reqparampanel thead td,
.reqbodyparam thead td,
.structexample thead td,
.apitool thead td {
  height: 28px;
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: #212121;
  line-height: 28px;
}
.reqparampanel tbody,
.reqbodyparam tbody,
.structexample tbody,
.apitool tbody {
  width: 100%;
  border-bottom: 1px #f2f2f2 solid;
}
.reqparampanel tbody tr,
.reqbodyparam tbody tr,
.structexample tbody tr,
.apitool tbody tr {
  height: 40px;
}
.reqparampanel tbody td,
.reqbodyparam tbody td,
.structexample tbody td,
.apitool tbody td {
  padding-right: 20px;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  font-family: SourceCodePro-Medium, SourceCodePro;
  font-weight: 500;
  color: #333333;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.reqparampanel tbody td span,
.reqbodyparam tbody td span,
.structexample tbody td span,
.apitool tbody td span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.reqparampanel td {
  width: 286px;
}
.reqbodyparam .subTableTd {
  display: flex;
  align-items: center;
}
.reqbodyparam .expansionIcon {
  display: inline-block;
  width: 16px;
  height: 16px;
}
.reqbodyparam .widthFlex {
  flex: 1;
  min-width: 170px;
}
.structexample {
  width: 82%;
}
.structexample .example .exampleHead {
  padding-left: 28px;
  border: 1px solid rgba(151, 151, 151, 0.5);
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #666666;
  display: flex;
  align-items: center;
}
.structexample .example .exampleHead .exampleHeadText {
  padding-top: 7px;
}
.structexample .example .exampleHead .exampleHeadText_click {
  padding-top: 7px;
  color: #276ff5;
}
.structexample .example .exampleHead .exampleHeadBorder {
  height: 3px;
  visibility: hidden;
}
.structexample .example .exampleHead .exampleHeadBorder_click {
  height: 3px;
  background-color: #276ff5;
  visibility: visible;
}
.structexample .example .exampleHead > div {
  padding-right: 40px;
  height: 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.structexample .example .exampleBody {
  border: 1px solid rgba(151, 151, 151, 0.5);
  border-top: 0;
  position: relative;
}
.structexample .example .exampleBody code {
  background: #f6f8fa;
  padding: 20px 0 20px 25px;
  font-size: 12px;
  font-family: SourceCodePro-Regular, SourceCodePro;
  font-weight: 400;
  line-height: 18px;
  padding-left: 28px;
}
.structexample .example .exampleBody img {
  width: 20px;
  height: 20px;
}
.structexample .example .copytoclipboard {
  position: absolute;
  top: 18px;
  right: 19px;
  color: #666666;
  display: flex;
}
.apitool {
  padding-left: 40px;
}
.apitool a {
  display: inline-block;
  border: 1px solid #5582f3;
  border-radius: 3px;
  padding: 4px 12px;
  margin-right: 10px;
  font-size: 14px;
  font-family: MicrosoftYaHei;
  color: #5582f3;
  cursor: pointer;
}
.apitool .aHide {
  display: none;
}
.anchorcontrol_init {
  visibility: hidden;
}
.anchorcontrol {
  visibility: visible;
  width: 160px;
  background-color: #fff;
  position: fixed;
  top: 0;
  z-index: 2;
}
.anchorcontrol li {
  height: 37px;
}
.anchorcontrol a {
  width: 100%;
  display: inline-block;
  border-left: 1px solid #98a1a8;
  padding-left: 9px;
  color: #98a1a8;
  height: 37px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  line-height: 37px;
}
.anchorcontrol a:hover,
.anchorcontrol a:active {
  color: #3863ff;
}
.anchorcontrol .aHover {
  color: #3863ff;
  border-color: #3863ff;
}
.alert {
  display: none;
}
